<template>
  <div class="container p-4">
    <div class="card">
      <div class="card-header">
        <h3 class="titre">Compte à Rebours</h3>
      </div>
      <div class="card-body">
        <p class="distance">{{distance}}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "countDownComponent",
  components: {},
  data() {
    return {
      distance: "---"
    };
  },
  watch: {},
  created() {
    this.do_1();
  },
  methods: {
    do_1() {
      let countDownDate = new Date("2021-12-01T00:00:00").getTime();
      let x = setInterval(() => {
        let now = new Date().getTime();
        let distance = countDownDate - now;
        let days = Math.floor(distance / (1000 * 60 * 60 * 24));
        let hours = Math.floor(
          (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
        );
        let minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        let seconds = Math.floor((distance % (1000 * 60)) / 1000);
        this.distance =
          days + "J " + hours + "H " + minutes + "M " + seconds + "S ";
        if (distance < 0) {
          clearInterval(x);
          this.distance = "EXPIRÉ";
        }
      }, 1000);
    }
  }
};
</script>

<style >
.titre {
  text-align: center;
  color: #000;
  padding: 0;
  margin: 0;
}
.distance {
  padding: 2rem 0;
  margin: 0;
  text-align: center;
  font-size: 3rem;
  color: #6200ee;
  font-weight: 700;
}
</style>
